<style lang="less" scoped>
    .home-box{
        position: relative;
        height: 100%;
        overflow: scroll;
    }
    header{
        height: 58px;
        padding: 0.6em 1em;
        font-size: 24px;
        color: white;
        width: 100%;
        background-color:rgb(43, 58, 66);
        .menu{
            display: none;
            cursor: pointer;
            &:hover{
                color: #a1e6ef;
            }
        }
    }
    .aside{
        width:280px;
        float: left;
        position: absolute;
        top: 58px;
        left: 0;
        bottom: 0;
        overflow: auto;
        transition: transform .2s ease;
        z-index: 5;
        background-color: white;
    }
    .mask{
        position: absolute;
        left: 0;
        top:0;
        right:0;
        bottom:0;
        z-index: 4;
        background-color: rgba(0,0,0,.4);
    }
    .aside.show{
        transform: translate3d(0,0,0);
    }
    @media screen and (max-width: 768px) {
        .aside{
            box-shadow: 0 0 15px rgba(0, 0, 0, 0.2);
            transform: translate3d(-300px,0,0);
        }
        header>.menu{
            display: inline;
        }
        .container{
            padding-left: 0!important;
        }

    }
    .container{
        position: absolute;
        top:58px;
        width: 100%;
        padding-left: 280px;
        bottom: 0;
        z-index: 1;
        overflow: scroll;
        transition: all .2s ease;
    }
    .main{
        height: 1200px;
    }

</style>

<template>
    <div class="home-box">
        <header>
            <i @click="menu"  class="el-icon-menu menu"></i> mock
        </header>
        <div class="aside" ref="aside" :class="{show:show}">
            <el-aside style="width: 280px" class="aside-box">
                <el-menu  default-active="1-4-1" class="el-menu-vertical-demo" :collapse="false">
                    <el-menu-item index="1" @click="addApi">
                        <i class="el-icon-edit"></i>
                        <span slot="title">新增接口</span>
                    </el-menu-item>
                    <el-menu-item index="2">
                        <i class="el-icon-more"></i>
                        <span slot="title">>></span>
                    </el-menu-item>

                </el-menu>
            </el-aside>
        </div>
        <transition name="fade">
            <div v-show="show" @click="menu" class="mask"></div>
        </transition>
        <div class="container">

            <transition  appear>
                <keep-alive>
                    <router-view></router-view>
                </keep-alive>
            </transition>
        </div>

    </div>
</template>
<script>
    import {mapState} from "vuex"
  export default {
    data() {
      return {
        show:''
      };
    },
    computed:{
        ...mapState(['addFlag'])
    },
    methods:{
      menu(){
        this.show = !this.show
      },
      addApi(){
        this.commit('addFlag',!this.addFlag);
        this.show = false;
      }
    },
  };

</script>



